<template>
  <nav class="mobile-bar iconfont">
    <i v-on:click="showMenu">&#xe681</i>
  </nav>
  <nav v-show="menuOpen"
       transition="expand"
       class="menu" >
    <ul>
      <li v-for="item in menu">
        <a v-link="{ path: item.href }" ><i class="{{'iconfont ' + item.icon}}"></i>{{item.name}}</a>
      </li>
    </ul>
  </nav>
</template>

<script>
  var menu = [
    {
      name: 'Home',
      icon: 'icon-icon61',
      href: '/'
    },
    {
      name: 'Profile',
      icon: 'icon-icon35',
      href: '/profile'
    },
    {
      name: 'About',
      icon: 'icon-icon22',
      href: '/about'
    }
  ]
  export default {
    data () {
      return {
        menu: menu,
        menuClass: 'menu',
        menuOpen: true
      }
    },
    methods: {
      showMenu: function (event) {
        this.menuOpen = !this.menuOpen
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" type="text/css" lang="less" scoped>
  .mobile-bar{
    display: none;
  }
  .menu {
    padding: 0.5rem 6rem;
    width: 100%;
    box-shadow: 0 0 4px rgba(0,0,0,0.25);

    h3 {
      font-size:16px ;
      color: #fff;
    }
    ul {
      display: flex;
      flex-flow: row nowrap;
      justify-content:space-around;
      li {
        display: inline-block;
        list-style: none;
        text-align: left;
        padding: 0.5rem 0;
        margin: 0 0.6em;
        a {
          color: #b8b8b8;
          font-size: 14px;
          font-weight: 100;

          i{
            padding-right: 0.5rem;
          }

          &::before{
            padding-right: 0.5rem;
          }
          &:hover,&:active {
            color: #000;
          }
        }
      }
    }
  }
  @media screen and (max-width: 720px){
    .mobile-bar{
      display: block;
      position: relative;
      z-index: 2;
      padding: 0.5rem 1rem;
      width: 100%;
      box-shadow: 0 0 4px rgba(0,0,0,0.25);

      i{
        display: inline-block;
        padding: 0.5rem 0;
        font-style: normal;
        color: #b8b8b8;
      }
    }
    .menu{
      position: absolute;
      z-index: 1;
      width: 50%;
      height: 100%;
      padding: 0 0.6rem;

      background: #fafafa;
      box-shadow: none;
      transform: translateX(-100%);

      ul{
        flex-flow: column nowrap;
        li{
          &::after{
            content: '';
            display: block;
            margin-top: 1rem;
            height: 1px;
            transform: scaleY(0.5);
            background: rgba(204,204,204,0.3);
          }
        }
      }


        /* 必需 */
        &.expand-transition {
          transition: all .3s ease;
          transform: translateX(0%);
        }

        /* .expand-enter 定义进入的开始状态 */
        /* .expand-leave 定义离开的结束状态 */
        &.expand-enter, &.expand-leave {
          transform: translateX(-100%);
        }


    }
  }
</style>



