import Vue from 'vue'
import VueRouter from 'vue-router'
import configRouter from './routes'
import App from './App'

Vue.use(VueRouter)

const router = new VueRouter({
  history: true
})

configRouter(router)

router.start(Vue.extend(App), '#root')

window.router = router



