import Home from './page/Home'
import Profile from './page/Profile'
import About from './page/About'
export default function (router) {
  router.map({
    '/': {
      component: Home
    },
    '/profile': {
      component: Profile
    },
    '/about': {
      component: About
    }
  })
}



